.pl{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    /* background-image: linear-gradient(rgba(255,255,255), rgba(200, 247, 248,1), rgba(255,255,255)); */

}

.pl-texts{
    width: 65%;
    align-items: center;
    text-align: center;

}
.pl-title{
    font-size: 50px;
    font-weight: 600;


}
.pl-subtitle{
    font-size: 40px;
    font-weight: 600;
    padding-bottom: 40px;
    color: blueviolet;
    /* color: rgb(121, 121, 121); */

}
.pl-subtitle-2{
    font-size: 40px;
    font-weight: 600;
    padding-bottom: 40px;
    padding-top: 40px;
    color: blueviolet;
    /* color: rgb(121, 121, 121); */

}
.pl-sample{
    font-size: 30px;

}


.pl-desc{
    margin: 20px 0px;

}
.pl-list{
    /* width: 100%; */


    display: flex;
    flex-wrap: wrap;
    padding: 0 4px;

    /* align-items: center;*/
    align-content: center;
    justify-content: space-around;

}

.pl-video{
    padding-top: 50px;
    align-content: center;

}
.pl-drone{
    font-size: 100;
    font-weight:600;
    color: gray;
    text-align: center;

}
.pl-software-list{
    text-align: left;
    font-weight: normal;
    font-size: 22px;
    margin-top: 30px;

}
