.c{
    height: 30vh;
    position: relative;
    /* background-image: linear-gradient(rgba(255,255,255), rgba(200, 247, 248,1)); */
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: row;
}
.c-bg{
    flex:1;
    clip-path: polygon(100% 0, 85% 50%, 100% 100%, 0 100%, 0 0);
    background-color: blueviolet;
    width: 5%;
    height: 100%;
}
.c-wrapper{
    flex:225;
    padding: 50px;
    align-content: center;
    align-items: center;

     display: flex;
     flex-direction: column;
    /*align-items: center;
    text-align: center; */


}

.c-info{
    width: 20%;
    align-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

}
.c-email{
    flex: 0.99;
}

.c-title{
    font-size: 50px;

    text-align: center;


}
.c-info-item{
    display: flex;
    align-items: center;
    /* align-items: center;
    align-self: center;
    text-align: center; */
    /* align-items: center;
    align-content: center; */
    /* margin:50px 0px; */
    /* font-weight: 400; */
    /* width: 100%; */
    background-color: cadetblue;
}
.c-icon{
    max-width: 30px;
    max-height: 30px;
    margin-right: 10px;
    align-self: center;
    flex:0.01;


}