.i{
    display: flex;
    height: 100vh;
    background-image: linear-gradient(rgba(200, 247, 248,1), rgba(255,255,255));
}


.i-left{
    flex: 1;
    display: flex;
    align-items: center;

}
.i-right{
    flex:  1;
    position: relative;
}
.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;

}

.i-intro{
    font-size: 40px;
    font-weight: 300;
}
.i-name{
    font-size: 120px;

}
.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;

}

@keyframes move{

    33%{
        transform: translateY(-50px);
    }
    67%{
         transform: translateY(-100px);
    }
    100%{
        transform: translateY(-150px);
    }


}
.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: blueviolet;
    display: flex;
    align-items: center;

}
.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;

}
.i-bg{
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%, 15% 50%);
    /* clip-path: polygon(100% 0%, 100% 48%, 100% 100%, 25% 100%, 0% 50%, 25% 0%); */
    background-color: blueviolet;
    width: 100%;
    height: 100%;
    top: 0px;
    right: 0px;
    position: absolute;
}