.p{
    width:30%;
    height: 100%;
    margin: 10px;
    background-color: rgba(200, 247, 248,0.2);
    /* background-color: rgba(137, 43, 226, 0.05); */
    border-radius: 10px;
    display: flex;
    align-items: center;
    overflow: show;

}
.p-img{
        /* flex: 33%; */
        margin-top: 20px;
        /* vertical-align: middle; */
        display: block;
        width: 100%;
        transition: all 0.5s ease;



}
.p-img:hover {
    z-index: 100;
    transform: scale(1.25);
  }


.p-info{
    font-weight:bold;
    font-size: 25px;

}
.p-column{
    flex: 30%;
    /* max-width: 30%; */
    padding: 0 4px;
    display: inline;

}
.p-vidBox{

    max-width: 100%;
    background-color: blueviolet;

}

.p-content{
    display: flex;
    justify-content: space-around;
    flex: 100%;
    max-width: 100%;
    /* max-height: 100% ; */

    padding: 0 4px;


}

.p-content-box{
    display: flex;
    /* align-items: center;
    width: 100%;
    height: 100%; */
    padding: 0 4px;
    flex-wrap: wrap;



}
